body {
  margin: 0;
  padding: 0;
  line-height: 1.3;
  overflow-x: hidden;
  opacity: 1;
 }

html, body {
  height: 100%;
}

.app-wrapper {
  display: grid;
  grid-template-areas:  "n h"
                        "n c ";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  max-width: 1400px;
  height: 100vh;
}

* {
  box-sizing: border-box;
}

.app-wrapper-content {
  grid-area: c;
  background-color: #f9f9ff;
}










